$colorGreen: #0c474b;
$fontSizeBig: 24px;
$fontSizeMedium: 20px;
$fontSizeSmall: 16px;

.section-1 {
  background-color: var(--background-color-light);
  text-align: var(--text-align-center);
  width: 100vw;
  height: 1024px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100vw;
  font-family: "Poppins";
  padding-bottom: 32px;
  color: $colorGreen;
}

.wrapper {
  grid-template-columns: 1fr;
  grid-template-columns: 100vw;
}

.title2 {
  margin: 41px auto auto auto;
  width: 322px;
  grid-column-start: 1;
  grid-column-end: 1;
  font-size: $fontSizeBig;
}

.table {
  margin: 37px auto auto auto;
  width: 90px;
  height: 90px;
  grid-column-start: 1;
  grid-column-end: 1;
}

.title-need {
  margin-top: 47px;
  grid-column-start: 1;
  grid-column-end: 1;
  font-size: $fontSizeMedium;
}

.text {
  margin: 12px auto auto auto;
  font-size: var(--font-size-);
  width: 208px;
  grid-column-start: 1;
  grid-column-end: 1;
}

.bag {
  margin: 40.5px auto auto auto;
  width: 90px;
  height: 90px;
  grid-column-start: 1;
  grid-column-end: 1;
}

.title-materias {
  margin: 42px auto auto auto;
  width: 291px;
  grid-column-start: 1;
  grid-column-end: 1;
  font-size: $fontSizeMedium;
}

.guide {
  margin: 40px auto auto auto;
  width: 90px;
  height: 90px;
  grid-column-start: 1;
  grid-column-end: 1;
}

.title-guide {
  margin: 37.5px auto auto auto;
  grid-column-start: 1;
  grid-column-end: 1;
  font-size: $fontSizeMedium;
}
